<script>
  import XLSX from 'xlsx'
  import FileSaver from 'file-saver'
  export default {
    data() {
      return {
        form1:{input1:''},
        checkList1: [],
        loading1:true,dialogTableVisible1:false,
        tableData1:[],
        form2:{input1:''},
        checkList2: [],
        loading2:true,dialogTableVisible2:false,
        tableData2:[],
        right0:true,right4:true,
      }
    },
    created(){
      this.convert1();
      this.convert2();
    },
    methods: {
      Daochu1() {
        var xlsxParam = { raw: true };//excel
        var wb = XLSX.utils.table_to_book(document.querySelector("#ssss"),xlsxParam);//outTableid
        var wbout = XLSX.write(wb, {
          bookType: "xlsx",
          bookSST: true,
          type: "array"
        });
        try {
          FileSaver.saveAs(
            new Blob([wbout], { type: "application/octet-stream;charset=utf-8" }),
            ".xlsx"
          );
        } catch (e) {
          if (typeof console !== "undefined") console.log(e, wbout);
        }
        return wbout;
      },
      Daochu2() {
        var xlsxParam = { raw: true };//excel
        var wb = XLSX.utils.table_to_book(document.querySelector("#tttt"),xlsxParam);//outTableid
        var wbout = XLSX.write(wb, {
          bookType: "xlsx",
          bookSST: true,
          type: "array"
        });
        try {
          FileSaver.saveAs(
            new Blob([wbout], { type: "application/octet-stream;charset=utf-8" }),
            ".xlsx"
          );
        } catch (e) {
          if (typeof console !== "undefined") console.log(e, wbout);
        }
        return wbout;
      },
      convert1: function () {
        var _this = this
        this.$axios.get('/Pdd/RightsMangement/in').then(resp => {
          if (resp && resp.status === 200) {
            _this.right0= !Boolean(Number(sessionStorage.getItem("right0")));
            _this.right4= !Boolean(Number(sessionStorage.getItem("right4")));
            if (!_this.right0)
            {
              _this.tableData1=resp.data
            }
            _this.loading1=false
          }
        })
      },
      convert2: function () {
        var _this = this
        this.$axios.get('/Pdd/RightsMangement/in2').then(resp => {
          if (resp && resp.status === 200) {
            _this.right0= !Boolean(Number(sessionStorage.getItem("right0")));
            _this.right4= !Boolean(Number(sessionStorage.getItem("right4")));
            if (!_this.right0)
            {
              _this.tableData2=resp.data
            }
            _this.loading2=false
          }
        })
      },
      subForm1() {
        if(this.form1.input1!==''){
          this.go1()
        }
        else
        {
          alert('')
        }
      },
      subForm2() {
        if(this.form2.input1!==''){
          this.go3()
        }
        else
        {
          alert('')
        }
      },
      go1(){
        var _this = this
        var res={id:this.form1.input1}
        for (var i=0;i<_this.checkList1.length;i++)
        {
            switch (_this.checkList1[i])
            {
              case '':
                res["right0"]='';
                break;
              case '':
                res["right1"]='';
                break;
              case '':
                res["right2"]='';
                break;
              case '':
                res["right3"]='';
                break;
              case '':
                res["right4"]='';
                break;
            }
        }
        this.$axios
          .post('/Pdd/RightsMangement/update', res)
          .then(successResponse => {
            if (successResponse.data.code == 200)
            {
              alert('')

            }
            else if (successResponse.data.code == 400)
            {
              alert('')
            }
            else if (successResponse.data.code == 401)
            {
              alert('')
            }
          })
          .catch(failResponse => {
          })
      },
      go3(){
        var _this = this
        var res={id:this.form2.input1}
        for (var i=0;i<_this.checkList2.length;i++)
        {
          switch (_this.checkList2[i])
          {
            case '':
              res["right0"]='';
              break;
            case '':
              res["right1"]='';
              break;
            case '':
              res["right2"]='';
              break;
            case '':
              res["right3"]='';
              break;
            case '':
              res["right4"]='';
              break;
          }
        }
        this.$axios
          .post('/Pdd/RightsMangement/update2', res)
          .then(successResponse => {
            if (successResponse.data.code == 200)
            {
              alert('')

            }
            else if (successResponse.data.code == 400)
            {
              alert('')
            }
          })
          .catch(failResponse => {
          })
      },
      go4() {
        for (let key in this.form1)
        {
          this.form1[key]='';
        }
        this.checkList1=[]
      },
      go5() {
        for (let key in this.form2)
        {
          this.form1[key]='';
        }
        this.checkList2=[]
      }
    }
  }
</script>
