              <el-table-column prop="downover" label="" width="60"></el-table-column>
              <el-table-column prop="pricecash" label="" width="60"></el-table-column>
              <el-table-column prop="pricedivided" label="" width="60"></el-table-column>
              <el-table-column prop="priceadd" label="" width="60"></el-table-column>
              <el-table-column prop="priceover" label="" width="60"></el-table-column>
              <el-table-column prop="upricecash" label="" width="60"></el-table-column>
              <el-table-column prop="upricedivided" label="" width="60"></el-table-column>
              <el-table-column prop="upriceadd" label="" width="60"></el-table-column>
              <el-table-column prop="upriceover" label="" width="60"></el-table-column>
              <el-table-column prop="allprice2" label="" width="60"></el-table-column>
              <el-table-column prop="otherprice" label="" width="60"></el-table-column>
              <el-table-column prop="settlementprice1" label="" width="60"></el-table-column>
              <el-table-column prop="remark" label="" width="60"></el-table-column>
              <el-table-column prop="director" label="" width="60"></el-table-column>
              <el-table-column prop="examine" label="" width="60"></el-table-column>
              <el-table-column prop="settlement" label="" width="60"></el-table-column>
              <el-table-column prop="people" label="" width="60"></el-table-column>
            </el-table>
          </el-dialog>
          </el-col>
          <el-col :span="12">
            <el-button @click="Daochu3" type="text" icon="el-icon-download" round > </el-button>
          </el-col>
        </el-row>
      </el-row>
    </el-card>
  </div>
</template>
<script>
  import XLSX from 'xlsx'
  import FileSaver from 'file-saver'
  export default {
    data() {
      return {
        form1:{input1:'', input2:'',input3:''},
        form2:{input1:'',input2:'',input3:''},
        form3:{input1:'',input2:'',input3:''},
        tableData1: [],
        tableData2: [],
        tableData3: [],
        loading1:false,dialogTableVisible1:false,
        loading2:false,dialogTableVisible2:false,
        loading3:false,dialogTableVisible3:false,
        right0:true,
      }
    },
    created()
    {
    this.convert1();
    },
    methods:{
      Daochu1() {
        var xlsxParam = { raw: true };//excel
        var wb = XLSX.utils.table_to_book(document.querySelector("#sss1"),xlsxParam);//outTableid
        var wbout = XLSX.write(wb, {
          bookType: "xlsx",
          bookSST: true,
          type: "array"
        });
        try {
          FileSaver.saveAs(
            new Blob([wbout], { type: "application/octet-stream;charset=utf-8" }),
            ".xlsx"
          );
        } catch (e) {
          if (typeof console !== "undefined") console.log(e, wbout);
        }
        return wbout;
      },
      Daochu2() {
        var xlsxParam = { raw: true };//excel
        var wb = XLSX.utils.table_to_book(document.querySelector("#sss2"),xlsxParam);//outTableid
        var wbout = XLSX.write(wb, {
          bookType: "xlsx",
          bookSST: true,
          type: "array"
        });
        try {
          FileSaver.saveAs(
            new Blob([wbout], { type: "application/octet-stream;charset=utf-8" }),
            ".xlsx"
          );
        } catch (e) {
          if (typeof console !== "undefined") console.log(e, wbout);
        }
        return wbout;
      },
      Daochu3() {
        var xlsxParam = { raw: true };//excel
        var wb = XLSX.utils.table_to_book(document.querySelector("#sss3"),xlsxParam);//outTableid
        var wbout = XLSX.write(wb, {
          bookType: "xlsx",
          bookSST: true,
          type: "array"
        });
        try {
          FileSaver.saveAs(
            new Blob([wbout], { type: "application/octet-stream;charset=utf-8" }),
            ".xlsx"
          );
        } catch (e) {
          if (typeof console !== "undefined") console.log(e, wbout);
        }
        return wbout;
      },
      convert1: function ()
      {
        var _this = this;
        this.$axios.get('/Pdd/rightinit').then(resp => {
          if (resp && resp.status === 200) {
            _this.right0 = !Boolean(Number(sessionStorage.getItem("right0")));
          }
        })
      },
      go1(){
        var _this = this
      if(_this.form1.input1==='')
      {_this.form1.input1=null}
        if(_this.form1.input2==='')
        {_this.form1.input2=null}
        this.$axios
          .post('/Pdd/CompanyQuery/in1', { name:this.form1.input1,
            address:this.form1.input2,
            measuredate:this.form1.input3})
          .then(resp => {
            if (resp && resp.status === 200) {
              _this.tableData1 = resp.data
              var i;
              for (i = 0; i < _this.tableData1.length; i++) {
                if (_this.tableData1[i].verify === 1) {
                  _this.tableData1[i].verify = ''
                } else if (_this.tableData1[i].verify === 0) {
                  _this.tableData1[i].verify = ''
                }
              }
            }
          })
        _this.loading1=false;
      },
      go2(){
        var _this = this
        this.$axios
          .post('/Pdd/CompanyQuery/in2', { id:this.form2.input1,
            mainid:this.form2.input2})
          .then(resp => {
            if (resp && resp.status === 200) {
              _this.tableData2 = resp.data
            }
          })
        _this.loading2=false;
      },
      go3(){
        var _this = this
        _this.loading3=true
        if(_this.form3.input3==='')
        {_this.form3.input3=null}
        this.$axios
          .post('/Pdd/PopulationQuery/in3', { id:this.form3.input1,
            mainid:this.form3.input2,
            name:this.form3.input3})
          .then(resp => {
            if (resp && resp.status === 200) {
              _this.tableData3 = resp.data
            }
          })
        _this.loading3=false;
      },
      clean1()
      {
        var _this=this;
        _this.tableData1=[]
      },
      clean2()
      {
        var _this=this;
        _this.tableData2=[]
      },
      clean3()
      {
        var _this=this;
        _this.tableData3=[]
      },
    }
  }
</script>
<style>
  .el-row {
    margin-bottom: 20px;
  &:last-child {
     margin-bottom: 0;
   }
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>




